body, html {
    margin: 0;
    padding: 0;
    height: 100%;
    font-family: Arial, sans-serif;
}

/*
.welcome-page {
    font-family: Arial, sans-serif;
}
*/
.page {
    display: flex;
    flex-direction: column;
    height: 100vh;
    background-image: url('background.jpg');
    background-size: cover;
    background-position: center;
}

.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #d2232a;
    padding: 10px 20px;
    color: white;
}

.header .logo img {
    height: 40px;
}

.header .nav ul {
    display: flex;
    list-style: none;

    /*new*/
    padding: 0;
    margin: 0;
}

.header .nav ul li {
    margin-right: 20px;
}

.header .nav ul li a {
    color: white;
    text-decoration: none;
}

.header .user-actions {
    display: flex;
    align-items: center;
}

.header .user-actions a {
    color: white;
    text-decoration: none;
    margin-left: 20px;
}

.main-content {
    /*new*/
    flex: 1;

    position: relative;
    text-align: center;
    color: white;
}


/*
.content {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}
*/
.content {
    position: relative;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
    max-width: 600px;
    padding: 20px;
    box-sizing: border-box;
}

.form {
    background-color: rgba(0, 0, 0, 0.6);
    padding: 20px;
    border-radius: 10px;
}

.form .form-group {
    margin-bottom: 15px;
}

.form .form-group label {
    display: block;
    margin-bottom: 5px;
    /*new*/
    color: white;
}

.form .form-group input {
    width: 100%;
    padding: 10px;
    border: none;
    border-radius: 5px;
    /*new*/
    box-sizing: border-box;
}

/*same*/
.form button {
    width: 100%;
    padding: 10px;
    background-color: #d2232a;
    border: none;
    border-radius: 5px;
    color: white;
    font-size: 16px;
    cursor: pointer;
}

/*new*/
.form button:hover {
    background-color: #b81b24;
}
