body, html {
    margin: 0;
    padding: 0;
    height: 100%;
    font-family: Arial, sans-serif;
}

.car-list-page {
    display: flex;
    flex-direction: column;
    height: 100vh;
    background-image: url('background.jpg');
    background-size: cover;
    background-position: center;
}

.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #d2232a;
    padding: 10px 20px;
    color: white;
}

.header .logo img {
    height: 40px;
}

.header .nav ul {
    display: flex;
    list-style: none;

    /*new*/
    padding: 0;
    margin: 0;
}

.header .nav ul li {
    margin-right: 20px;
}

.header .nav ul li a {
    color: white;
    text-decoration: none;
}

.header .user-actions {
    display: flex;
    align-items: center;
}

.header .user-actions a {
    color: white;
    text-decoration: none;
    margin-left: 20px;
}

.main-content {
    padding: 20px;
}

.main-content h1 {
    color: #d2232a;
}

.car-list {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
}

.car-card {
    border: 1px solid #ddd;
    border-radius: 5px;
    overflow: hidden;
    width: 300px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

.car-card img {
    width: 100%;
    height: auto;
}

.car-details {
    padding: 15px;
}

.car-details h2 {
    margin: 0 0 10px;
    font-size: 20px;
}

.car-details p {
    margin: 0 0 5px;
}

.car-properties p {
    margin: 5px 0;
}

.car-card button {
    display: block;
    width: 100%;
    padding: 10px;
    background-color: #e60000;
    color: white;
    border: none;
    cursor: pointer;
    font-size: 16px;
    text-align: center;
}

.car-card button:hover {
    background-color: #cc0000;
}
